import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LightUser } from './../../../light/light-out/light-out.module';

@IonicPage()
@Component({
    selector: 'light-list-page',
    templateUrl: 'light-list-page.html',
})
export class LightListPage {


    //标题名称
    titlename: string = "列表例子";

    //数据的列表
    datalist: Array<any> = [];

    //数据连接
    dataurl: string = "DelegateProject/GetDelegateProjectList";

    //id的列信息
    idcol: string = "CODE";

    //搜索的内容
    searchQuery: string = "";

    //显示的信息
    placeholder: string = "请输入项目名称、客户单位、地点";

    //数据的参数信息
    dataParam: any = {};

    /**
     * 项目的列表信息
     * @param {NavController} navCtrl 
     * @param {NavParams} navParams 
     * @memberof ProjectListPage
     */
    constructor(
        public navCtrl: NavController,
        public navParams: NavParams,
        public lightUser: LightUser
    ) {
    }

    ngOnInit() {
        this._flush();
    }

    /**
      * 文本的搜索
      * 
      * @param {*} searchbar 
      * @memberof ProjectListPage
      */
    public searchText(searchbar: any): void {
        this._flush();
    }

    /**
     * 修改参数，刷新组件展示数据
     * 
     * @private
     * @memberof SProjectListPage
     */
    private _flush(): void {
        var searchParam: any = {};
        if (!!this.searchQuery) {
            searchParam["search"] = this.searchQuery;
        }
        searchParam["userName"] = this.lightUser.getUserName();
        this.dataParam = searchParam;
    }

}



